.friends {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
}

.friends__text {

}

.friends__item {
    text-align: center;
    width: 70px;
    height: 70px;
    margin-bottom: 25px;
}

.friends__item img {
    width: 50px;
    height: 50px;
    border: black solid 1px;
    border-radius: 50%;
}

.friends__item__name {
    margin-bottom: 8px;
}